import { Component, OnInit } from '@angular/core';
import {SweetAlertService} from 'angular-sweetalert-service';
import {ActivatedRoute, Router} from '@angular/router';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {FileService} from '../../../providers/file.service';
import {EmpresaService} from '../../../providers/empresa.service';
import {DataTableResource} from '../../../shared/components/data-table';
import {DataTableTranslations} from '../../../shared/components/data-table/components/types';

@Component({
  selector: 'app-partidas',
  templateUrl: './partidas.component.html'
})
export class PartidasComponent implements OnInit {
  saveForm: FormGroup;
  private message: any;
  private role: any;
  private sub: any;
  private idPartida:number;
  private registroControl:number;
  private cuentaContable:string;
  private importe:number;
  private referencia:string;
  private dataSourceRegistro: any;
  private loaded: boolean;
  private itemCount:number = 0;
  private rows=[];
  private dataSourceEmpresa:any;
  private IdEmpresaSeleted:string;
  private defaultTranslations = <DataTableTranslations>{
    indexColumn: 'Indice',
    selectColumn: 'Seleccionar',
    expandColumn: 'Expandir',
    paginationLimit: 'Partidas por página',
    paginationRange: 'Total de partidas'
  };

  

  constructor(private route: ActivatedRoute, private fileServ: FileService,private empresaServ:EmpresaService,private alertService: SweetAlertService) {
    this.loaded = false;
  }
  getEmpresas(){
      this.empresaServ.get().subscribe(res => {
        if (res.Result) {
          this.dataSourceEmpresa=res.Result;
          this.IdEmpresaSeleted= this.dataSourceEmpresa[0].CE_ID_EMPRESA;
        }
      });
  }

  onChangeEmpresa(valueSelect){
    this.IdEmpresaSeleted=valueSelect;
  }

  getPartida(){
    if(this.idPartida!=null && this.idPartida>0){
      this.fileServ.getPartidaService(this.idPartida).subscribe(res => {
        if (res.Result) {
          this.dataSourceRegistro = new DataTableResource(res.Result);
          this.dataSourceRegistro.count().then(count => this.itemCount = count);
          this.loaded = true;
        } else {
          this.alertService.warning({
            title: res.Result.Messaje || 'El archivo  no cumple con el formato requerido.'
          });
        }
      });
    }
  }

  reloadItems(params) {
    if (this.dataSourceRegistro) {
      this.dataSourceRegistro.query(params).then(rows => this.rows = rows);
    }
  }

  onClickBuscar(){
    if(this.idPartida!=null && this.idPartida>0){
      console.log(this.cuentaContable);
      console.log(this.importe);
      console.log(this.referencia);
      this.fileServ.findPartidaService(this.idPartida,this.IdEmpresaSeleted,
        this.cuentaContable===''?null:this.cuentaContable,
        this.importe,
        this.referencia===this.referencia?null:this.referencia).subscribe(res => {
        if (res.Result) {
          this.dataSourceRegistro = new DataTableResource(res.Result);
          this.dataSourceRegistro.count().then(count => this.itemCount = count);
          this.reloadItems({});
          this.loaded = true;
        } else {
          this.alertService.warning({
            title: res.Result.Messaje || 'Error en la consulta, vuelta a intentar.'
          });
        }
      });
    }

  }


  ngOnInit() {
    this.sub = this.route.params.subscribe(params => {
      this.idPartida=params['id'];
        });
        this.getPartida();
        this.getEmpresas();
        this.cuentaContable=null;
        this.importe=null;
        this.referencia=null;
  }

}



