import {CommonModule} from '@angular/common';
import {NgModule} from '@angular/core';
import {Routes, RouterModule} from '@angular/router';
import {InitialChargeComponent} from './component';
import {ListaErroresComponent} from './lista-errores.component';
import {PartidasComponent} from './partidas.component';
import {DataTableModule} from '../../../shared/components/data-table';
import {Ng2TableModule} from "ng2-table";
import {FormsModule} from "@angular/forms";
import {PaginationModule} from "ngx-bootstrap/pagination";
import {Select2Module} from 'ng2-select2';

const TABLE_ROUTES: Routes = [
  {path: '', component: InitialChargeComponent},
  {path: 'lista-errores/:nameFile', component: ListaErroresComponent},
  {path: 'partidas/:id', component: PartidasComponent}
];

@NgModule({
  declarations: [
    InitialChargeComponent,
    ListaErroresComponent,
    PartidasComponent
  ],
  imports: [
    CommonModule,
    FormsModule,
    PaginationModule.forRoot(),
    DataTableModule,
    Ng2TableModule,
    RouterModule.forChild(TABLE_ROUTES),
    Select2Module
  ]
})

export class InitialChargeModule {
}



