import { Component, OnInit } from '@angular/core';
import {Http} from "@angular/http";
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {Select2OptionData} from 'ng2-select2';
import {AreaOperativaService} from '../../../providers/areaOperativa.service';
import {FileService} from '../../../providers/file.service';
import {SweetAlertService} from 'angular-sweetalert-service';
import {DataTableResource} from '../../../shared/components/data-table';
import {DataTableTranslations} from '../../../shared/components/data-table/components/types';

@Component({
  selector: 'app-table',
  templateUrl: './component.html'
})
export class InitialChargeComponent implements OnInit {

  saveForm: FormGroup;
  nameFileError: string;
  private sub: any;
  private message: any;
  private messageAlert:string;
  private alertErrorDisplay:boolean;
  private loaded: boolean;
  private modelSelectAreaOperativa:any;
  private fileName: string;
  private validFormatsFile: Array<string>;
  private fileTmp: any;
  private registroControl:number;
  private dataSourceRegistro: any;
  private  itemCount:number = 0;
  private rows=[]
  public fileError:String;
  public areaOperativaSelect:string;

  defaultTranslations = <DataTableTranslations>{
    indexColumn: 'Indice',
    selectColumn: 'Seleccionar',
    expandColumn: 'Expandir',
    paginationLimit: 'Registros por página',
    paginationRange: 'Total de registros'
  };

  constructor(private http: Http, private areaOperativaServ: AreaOperativaService, 
              private fileServ: FileService,private alertService: SweetAlertService) {
   
    this.loaded= false;
    this.fileName="Seleccione Archivo";
    this.validFormatsFile= ['xlsx', 'xls'];
    this.alertErrorDisplay=false;
    this.fileError="default";
  }
  ngOnInit() {
    this.getRegistroControl();
    this.getAreaOperativa();
    console.log('ngOnInit');
    this.nameFileError="parameterTest";
  }

  getAreaOperativa() {
    this.areaOperativaServ.get().subscribe(res => {
      if (res.Result) {
        this.modelSelectAreaOperativa= res.Result;
        this.areaOperativaSelect=this.modelSelectAreaOperativa[0].CA_COD_AREA;
      }
    });
  }

  onChangeFile(event) {
    var files = event.srcElement.files;
    
    if(files){
      this.fileTmp= files[0];
      var ext = files[0].name.substring(files[0].name.lastIndexOf('.') + 1).toLowerCase();
      var FileIsValidate=this.validFormatsFile.indexOf(ext) !== -1;
      if(FileIsValidate){
        this.fileName=files[0].name;
        this.alertErrorDisplay=false;
      }else{
        this.messageAlert="El archivo "+files[0].name+" no es válido";
        this.alertErrorDisplay=true;
      }
     
    }
  }

  getRegistroControl(){
      this.fileServ.getRegistroControl().subscribe(res=>{
        if (res.StatusDescription === 'OK') {
            this.loaded = false;
            this.dataSourceRegistro = new DataTableResource(res.Result);
            this.dataSourceRegistro.count().then(count => this.itemCount = count);
            this.reloadItems({});
            this.loaded = true;
        }else 
        {
          console.log('PROFILE POST RES:');
          this.message = {text: res.error_description || 'No fue posible consultar el registro control', type: 'warning'};
        }
      });

  }

  onClickCargar(event){
    let formData = new FormData();
    formData.append("file", this.fileTmp);
    this.fileServ.save(formData,this.areaOperativaSelect).subscribe(res=>{
      if (res. StatusDescription === 'OK') {
        if(res.Result && res.Result.Messaje && res.Result.litsError.length>0){
          this.alertService.warning({
            title: res.Result.Messaje || 'El archivo  no cumple con el formato requerido.'
          });
        }else{
          this.registroControl=res.RegistroControl;
          this.getRegistroControl();
          this.alertService.success({
            title: res.Result.Messaje || 'El archivo fue cargado exitosamente.'
          });
        }
        console.log('subio el archivo');
      } else {
        console.log('PROFILE POST RES: ', res);
        this.message = {text: res.error_description || 'No fue posible Agregar el rol', type: 'warning'};
      }
    });
  }

  onChangeAreaOperativa(valueSelect){
    this.areaOperativaSelect=valueSelect;
  }
  
  reloadItems(params) {
    if (this.dataSourceRegistro) {
      this.dataSourceRegistro.query(params).then(rows => this.rows = rows);
    }
  }



}



