import { Component, OnInit } from '@angular/core';
import {SweetAlertService} from 'angular-sweetalert-service';
import {ActivatedRoute, Router} from '@angular/router';

@Component({
  selector: 'app-lista-errores',
  templateUrl: './lista-errores.component.html'
})
export class ListaErroresComponent implements OnInit {
  private message: any;
  private role: any;
  private sub: any;
  private nameFileError: string;
  constructor(private route: ActivatedRoute, private alertService: SweetAlertService) {
    
  }

  ngOnInit() {
    this.sub = this.route.params.subscribe(params => {
          console.log(params['nameFileError']);
        });
  }

}



